          p.mb-0 Por ejemplo, Volta ha diseado una batera que se puede recargar en un bao de agua sal y vinagre, sin embargo, la batera es totalmente reciclable. Para el desarrollo, la empresa utiliz un generador de biomolculas Ingeo certificado por C2C; el diseo permite que se cambien las piezas y los componentes de la carcasa, que se pueden quitar y descargar para su recuperacin y reutilizacin.
